.LayoutsHeader {
   
    width: 1519.2px;
    height: 64px;
    padding: 0 24px;
    background-color: #fff;
    top: 0;
}

.Cannvos {
    position: relative;
    height: 200px;
    color: #f4f4f4;
    border-radius: 5px;
    border: solid 1px #f0f0f0 ;
    margin-bottom: 50px;
}

.Image {
    position: absolute;
    height: 200px;
    width: 600px;
    left: 50%;
    margin-left: -300px;
    color: rgba(0.5,0.5,0.5,0.5);
    border-radius: 5px;
    border: solid 1px #f0f0f0 ;
}

.Image h1 {
    text-align: center;
}

.Image h5 {
    text-align: center;
}


