* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.CollegeList ul {
    height: 119px;
    width: 628px;
    list-style: none; 
    background-color: white;
    border-radius: 5px;
    padding: 10px 10px 9px 10px;
    overflow: hidden;
}

.CollegeList li {
    float:left;
    height: 100px;
    width: 68px;
    margin-right: 3px;
}
